export function mockForExampleGetColumns(Mock, url, query, options) {
  const columns = [
    {
      label: '',
      prop: 'name'
    },
    {
      label: '',
      prop: 'id'
    },
    {
      label: '',
      prop: 'sex',
      filters: [{text: '', value: 1}, {text: '', value: 0}]
    },
    {
      label: '',
      prop: 'date',
      sortable: true
    },
    {
      label: '',
      prop: 'county'
    },
    {
      label: '',
      prop: 'info'
    },
    {
      label: '',
      prop: 'created'
    }
  ]
  return {
    code: 0,
    data: columns
  }
}

