// 
export function mockForExampleGetTree(Mock, url, query, options) {
  const {parentId, count} = query
  const total = count || 10
  const root = Mock.mock({
    [`list|${total}`]: [{
      id: '@guid',
      parentId: function () {
        return parentId || null
      },
      label: '@ctitle',
      value: '@guid',
      created: '@date(yyyy-MM-dd hh:mm:ss)',
      isLeaf: false
    }]
  })

  return {
    code: 0,
    data: root.list
  }

}

// 
export function mockForExampleGetOptions(Mock, url, query, options) {
  const total = 50
  const rootCount = 6
  let index = 0
  const root = Mock.mock({
    [`list|${total}`]: [{
      id: function () {
        return ++index
      },
      parentId: function () {
        return this.id > rootCount ? Math.floor(Math.random() * this.id) : null
      },
      label: '@ctitle',
      value: function () {
        return this.id
      }
    }]
  })

  return {
    code: 0,
    data: root.list
  }
}

