import {
  Vue,
  Vuex,
  Router,
  progress,
  Access
} from './enhance'

export {
  Vue,
  Vuex,
  Router,
  progress,
  Access
}

export const getView = require('$ui/import/view/' + process.env.NODE_ENV)

/**
 * 
 * @param {object} params
 */
export function appRender({el, AppMaster, store, router, access, scale}) {
  return new Vue({
    el: el,
    components: {
      AppMaster
    },
    store,
    router,
    access,
    render(h) {
      const props = {...scale}
      return h(AppMaster, {props})
    }
  })
}
